/*
 *   Validation User from Email page
 */
 <style scoped lang="scss">
.center-container {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;
}
.validate {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  line-height: 1em;
  width: 100px;
  margin-top: 25%;

  p {
    color: lightgrey;
  }

  .md-progress-spinner {
      margin-top: 1em;
      margin-left: 20px;
  }
}
</style>
 
 <template>
     <div class="center-container">
         <div class="validate">
            <p>Validating</p>
            <md-progress-spinner class="md-accent" md-mode="indeterminate"></md-progress-spinner>
         </div>
     </div>
 </template>
 
 <script>
export default {
  name: "Validation",
  data() {
    return {
      email: this.$route.query.mail,
      token: this.$route.query.token
    };
  },
  mounted: function() {
    if (this.email && this.token) {
      const { email, token } = this;
      this.$store.dispatch("auth/AUTH_VALIDATE", { email, token }).then(() => {
        this.$router.push("/intranet/dashboard");
      });
    }
  }
};
</script>
 